.open-button {
    background-color: rgb(41, 110, 138);
    color: white;
    padding: 16px 20px;
    border: none;
    cursor: pointer;
    opacity: 0.8;
    position: absolute;
    bottom: 23px;
    right: 28px;
    width: 90px;
    border-radius: 100px;
  }
  
  .chat-popup {
    display: none;
    position: absolute;
    bottom: 0;
    right: 15px;
    border: 3px solid #f1f1f1;
    z-index: 9;
    border-radius: 10px;
  }
  
  .form-container {
    max-width: 300px;
    padding: 10px;
    background-color: white;
    border-radius: 50px;
  }
  
  .form-container textarea {
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    border: none;
    background: #f1f1f1;
    resize: none;
    min-height: 200px;
  }
  
  .form-container textarea:focus {
    background-color: #ddd;
    outline: none;
  }
  
  .form-container .btn {
    background-color: rgb(157, 175, 76);
    color: white;
    padding: 16px 20px;
    border: none;
    cursor: pointer;
    width: 100%;
    margin-bottom:10px;
    opacity: 0.8;
    border-radius: 30px;
  }
  
  .form-container .cancel {
    background-color: purple;
  }
  
  .form-container .btn:hover, .open-button:hover {
    opacity: 1;
  }